html,
body {
  background-color: #fff;
  height: 100%;
  width: 100%;
}
.lato {
  font-family: 'Lato', sans-serif;
  opacity: 0;
  transition: opacity 500ms, font-size 1000ms;
}
.lato._done {
  opacity: 1;
}
a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  background-color: #fff;
  color: #000;
  display: inline-block;
  padding: 0 .15em;
  margin: 0 -0.15em;
  transition: color 500ms, background-color 500ms;
}
.view {
  box-sizing: border-box;
  border-color: #fff;
  border-style: solid;
  border-width: 5px 0 0;
  overflow: auto;
}
.view.full {
  min-height: 100%;
  width: 100%;
  position: relative;
}
.view.full .mw960 {
  max-width: 960px;
  margin: auto;
  width: 100%;
}
.view.full .ph2em {
  box-sizing: border-box;
  padding-left: 2em;
  padding-right: 2em;
}
.view .table {
  display: table;
}
.view .cell {
  display: table-cell;
  padding-right: 1em;
}
.view .cell:last-child {
  padding-right: 0;
}
header {
  background-color: #6b6b6b;
  border-width: 0 !important;
  padding: 2em;
  text-align: center;
  transition: opacity 1.5s 1s, height 1s 1s, min-height 1s 1s, font-size 1s 0, border-width 1s;
}
header h1 {
  color: #fff;
  font-weight: 100;
  font-size: 6em;
  line-height: 0.75em;
  vertical-align: middle;
  margin: auto;
  height: 1em;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
}
body.fix .view {
  padding-bottom: 4em;
}
body.fix header {
  min-height: 0 !important;
  height: 2em !important;
  width: 100%;
  border-color: #fff;
  border-style: solid;
  border-width: 2px 0 0;
  position: fixed;
  bottom: 0;
  left: 0;
}
body.fix header h1 {
  font-size: 2em;
}
body.fix header.view {
  padding-bottom: 2em;
}
section h2 {
  font-family: 'Lato', sans-serif;
  opacity: 0;
  transition: opacity 500ms, font-size 1000ms;
  opacity: 1;
  transition: none;
  color: #fff;
  font-size: 3.5em;
  font-weight: 100;
  margin: .5em auto;
  text-align: center;
}
section h2._done {
  opacity: 1;
}
section.card {
  background-color: #45ba55;
  color: #fff;
  padding-top: 1.4375em;
  padding-bottom: 2.875em;
}
section.card address {
  color: inherit;
  font-style: normal;
  font-family: 'Lato';
  min-width: 300px;
  max-width: 600px;
  margin: auto;
}
section.card address .fn,
section.card address .email {
  font-size: 1.5em;
}
section.card address a:hover {
  color: #45ba55;
}
section.card address .adr {
  display: none;
}
section.card address .table {
  width: 100%;
  table-layout: fixed;
}
section.card address .table .cell {
  box-sizing: border-box;
  width: 50%;
}
section.card address .table .cell:first-child {
  text-align: right;
}
section.card address .table .cell:last-child {
  padding-left: 1em;
}
section.portfolio {
  background-color: #465075;
}
.example01 {
  background-color: #1e4f98;
  padding: 1em;
}
.example01 .tagbox {
  background-color: #fff;
  border: none;
  float: left;
  font-size: 1.5em;
  margin: 0 .5em .5em;
  padding: 14px .5em;
}
